@use "styles/stylesFunctions.scss" as stylesFunctions;
.body {
  background: #f8f8f8 0% 0% no-repeat padding-box;
  @include stylesFunctions.paddingY(75px);
}
.headers {
  h3,
  p {
    margin: 0;
    text-align: center;
  }
  h3 {
    @include stylesFunctions.fontStyles(60px, 50px, 600, #000000, #000);
  }
  p {
    margin-top: 20px;
    @include stylesFunctions.fontStyles(35px, 26px, 300, #000000, #000);
  }
  margin-bottom: 65px;
}

.tabBtn {
  display: block;
  width: 100%;
  background: #ffaa03 0% 0% no-repeat padding-box;
  border-radius: 10px;
  opacity: 1;
  padding: 17px 23px;
  margin-right: 10px;
  text-decoration: none;
  text-align: center;
  @include stylesFunctions.fontStyles(29px, 22px, 300, #ffffff, #000);
  &:hover {
    color: white;
  }
  svg {
    margin-right: 14px;
  }
}

.formContainer {
  margin-top: 10px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 10px;
  opacity: 1;
  @include stylesFunctions.paddingY(65px);
  .form {
    width: 100%;
    row-gap: 35px;
    @include stylesFunctions.lg() {
      width: 50%;
      margin: auto;
    }
    button {
      width: 100%;
      text-align: center;
      background: var(--unnamed-color-001736) 0% 0% no-repeat padding-box;
      background: #001736 0% 0% no-repeat padding-box;
      border-radius: 3px;
      opacity: 1;
      @include stylesFunctions.paddingY(23px);
      @include stylesFunctions.fontStyles(33px, 25px, 500, white, #000);
    }
    h3,
    p {
      margin: 0;
      text-align: center;
    }
    h3 {
      @include stylesFunctions.fontStyles(60px, 50px, 600, #000000, #000);
    }
    p {
      margin-top: 20px;
      @include stylesFunctions.fontStyles(35px, 26px, 300, #000000, #000);
    }
  }
}

.search {
  position: relative;
  input,
  select {
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    border-radius: 5px;
    height: 75px;
    width: 100%;
    padding: 10px;
    padding-right: 70px;
  }
  svg {
    position: absolute;
    right: 10px;
    top: 10px;
    transform: scale(0.7);
  }
}

.select {
  select {
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    border-radius: 5px;
    height: 75px;
    width: 100%;
    padding: 10px;

    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("data:image/svg+xml;utf8,<svg fill='grey' height='42'  width='42' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 25px;
    @include stylesFunctions.rtl(background-position-x, 25px, initial);
    @include stylesFunctions.rtl(background-position-x, initial, 560px);

    background-position-y: 20px;
    border: 1px solid #dfdfdf;
    border-radius: 2px;
    padding: 1rem;
    padding-right: 2rem;
  }
}
